import React from "react";
import rotate from "./icons/rotate_icon.png";
// import expand from "./icons/expand_icon.png";
import expand from "./icons/expand_icon.png";

import "./item.styles.css";

export const Item = (props) => {
  let wrapper, modal, close, image, footer;
  if (props.modal) {
    footer = "footer";
    wrapper = "wrapper";
    modal = "modal";
    close = "close-btn";
    image = "";
  } else {
    wrapper = "none";
    modal = "none";
    close = "none";
    footer = "image-footer";
    image = "image";
  }
  return (
    <div className={`item ${wrapper}`}>
      <img
        id={props.id}
        src={`${props.photo.largeImageURL}`}
        alt="img"
        className={`${image} ${modal}`}
        style={props.rotateStyle}
      />
      <div className={`${footer}`}>
        {!props.modal ? (
          <img
            id={props.id}
            src={expand}
            alt="expand icon"
            className="expand-icon"
            onClick={props.handleExpandClick}
          />
        ) : null}
        {!props.modal ? (
          <img
            id={props.id}
            src={rotate}
            alt="rotate icon"
            className="rotate-icon"
            onClick={props.handleRotateClick}
          />
        ) : null}
        {props.modal ? (
          <button
            type="button"
            className={`${close}`}
            onClick={props.handleExpandClick}
          >
            Close
          </button>
        ) : null}
      </div>
    </div>
  );
};
