import React, { Component } from "react";
import { debounce } from "lodash";
import { SearchBox } from "./components/search-box/search-box.component";
import { ItemList } from "./components/item-list/item-list.component";
import { Item } from "./components/item/item.component";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      photos: [],
      searchField: "",
      apiUrl: "https://pixabay.com/api",
      apiKey: "22698874-5e3ff1ef81847d81571af2bbf",
      rotation: 0,
      showModal: false,
      rotId: 0,
      modalId: 0,
    };
  }
  debounceEvent(...args) {
    this.debouncedEvent = debounce(...args);
    return (e) => {
      e.persist();
      return this.debouncedEvent(e);
    };
  }

  componentWillUmaount() {
    this.debouncedEvent.cancle();
  }

  handleChange = (event) => {
    let val = event.target.value;
    this.setState({ searchField: val });
    if (val === "") {
      this.setState({ photos: [] });
    } else {
      console.log(event.target.value);
      fetch(
        `${this.state.apiUrl}/?key=${this.state.apiKey}&q=${val}&image_type=photo&per_page=20`
      )
        .then((res) => res.json())
        .then((data) => this.setState({ photos: data.hits }))
        .catch((err) => console.error(err));
    }
  };

  handleRotateClick = (photo) => {
    let newRotation = this.state.rotation - 90;
    if (newRotation === -360) newRotation = 0;

    let updateRotId = photo.id;
    this.setState({
      rotation: newRotation,
      rotId: updateRotId,
    });
  };

  handleExpandClick = (photo) => {
    let updateModel = !this.state.showModal;
    let updateId = photo.id;
    this.setState({ showModal: updateModel, modalId: updateId });
  };

  render() {
    const images = this.state.photos.map((photo, index) => {
      let newStyle, newModal;

      if (photo.id === this.state.rotId) {
        newStyle = { transform: `rotate(${this.state.rotation}deg)` };

        console.log(index);
      }
      if (photo.id === this.state.modalId) {
        newModal = this.state.showModal;
      }
      return (
        <Item
          key={photo.id}
          id={photo.id}
          photo={photo}
          modal={newModal}
          rotateStyle={newStyle}
          handleExpandClick={() => this.handleExpandClick(photo)}
          handleRotateClick={() => this.handleRotateClick(photo)}
        />
      );
    });

    return (
      <div className="App">
        <h1>Photo Gallery</h1>
        <SearchBox handleChange={this.debounceEvent(this.handleChange, 1000)} />
        {this.state.searchField.length > 0 ? (
          <ItemList photos={images} />
        ) : null}
      </div>
    );
  }
}

export default App;
